  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}


.flex-container {
  display: flex;
}



.flex-child:first-child {
  background-color: #469931;
  width: 300px;
  height: 100%;
  position: fixed;
  
  
} 
