  width: 100%;
  height: 2000px;

}
.flex-child.green img {
  width: 150px;
  border-radius: 60%;
  position: fixed;
  left: 60px;
  margin-top: 150px;
  border: 10px solid rgba(190, 190, 190, .3);



}
.linkdiv {
